import { TOKEN_NAME } from "../constants";
import Storage from "../utils/Storage";
const SERVER_URL = process.env.NODE_ENV === "production"
    ? process.env.SERVER_URL_PROD
    : process.env.SERVER_URL_DEV;
function getAuthToken() {
    return Storage.get(TOKEN_NAME);
}
export function getStatusRequest() {
    return fetch(`${SERVER_URL}/api/status`);
}
export function textToSpeechRequest(params) {
    const token = getAuthToken();
    return fetch(`${SERVER_URL}/api/text-to-speech`, {
        method: "POST",
        body: JSON.stringify(params),
        headers: {
            "Authorization": `Bearer ${token}`,
            "Content-Type": "application/json"
        }
    });
}
// User/Password login
export function checkAuthRequest() {
    const token = getAuthToken();
    return fetch(`${SERVER_URL}/api/check-auth`, {
        // credentials: "include",
        headers: {
            "Authorization": `Bearer ${token}`
        }
    });
}
export function loginRequest({ username, password }) {
    return fetch(`${SERVER_URL}/api/login`, {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
        },
        // credentials: "include",
        body: JSON.stringify({ username, password })
    });
}
